import Cell from '../../cell'
import { UtilTools } from '../../tools'

const props = {
  // 
  colId: [String, Number],
  //  seq,radio,checkbox,expand
  type: String,
  //  v3.0  prop
  prop: String,
  //  v3.0  label
  label: String,
  // 
  field: String,
  // 
  title: String,
  // 
  width: [Number, String],
  // 
  minWidth: [Number, String],
  // 
  resizable: { type: Boolean, default: null },
  // 
  fixed: String,
  // 
  align: String,
  // 
  headerAlign: String,
  // 
  footerAlign: String,
  // 
  showOverflow: { type: [Boolean, String], default: null },
  // 
  showHeaderOverflow: { type: [Boolean, String], default: null },
  // 
  showFooterOverflow: { type: [Boolean, String], default: null },
  //  className
  className: [String, Function],
  //  className
  headerClassName: [String, Function],
  //  className
  footerClassName: [String, Function],
  // 
  formatter: [Function, Array, String],
  // 
  seqMethod: Function,
  //  v3.0  indexMethod
  indexMethod: Function,
  // 
  sortable: Boolean,
  //  v3 
  remoteSort: { type: Boolean, default: null },
  //  v3 
  sortBy: [String, Array],
  // 
  sortType: String,
  //  v3 
  sortMethod: Function,
  // 
  filters: { type: Array, default: null },
  // 
  filterMultiple: { type: Boolean, default: true },
  // 
  filterMethod: Function,
  // 
  filterResetMethod: Function,
  // 
  filterRecoverMethod: Function,
  // 
  filterRender: Object,
  // 
  treeNode: Boolean,
  // 
  visible: { type: Boolean, default: null },
  // 
  exportMethod: Function,
  // 
  footerExportMethod: Function,
  // 
  titleHelp: Object,
  // 
  cellType: String,
  // 
  cellRender: Object,
  // 
  editRender: Object,
  // 
  contentRender: Object,
  // 
  params: Object
}

const watch = {}
Object.keys(props).forEach(name => {
  watch[name] = function (value) {
    this.columnConfig.update(name, value)
  }
})

export default {
  name: 'VxeColumn',
  props,
  provide () {
    return {
      $xecolumn: this
    }
  },
  inject: {
    $xetable: {
      default: null
    },
    $xecolumn: {
      default: null
    }
  },
  watch,
  created () {
    this.$xetable.isSC = true
    this.columnConfig = this.createColumn(this.$xetable, this)
  },
  mounted () {
    UtilTools.assemColumn(this)
    if (this.type === 'expand' && !this.$scopedSlots.content && this.$scopedSlots.default) {
      UtilTools.warn('vxe.error.expandContent')
    }
  },
  destroyed () {
    UtilTools.destroyColumn(this)
  },
  render (h) {
    return h('div', this.$slots.default)
  },
  methods: Cell
}
